.home {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* width: 96vw; */
}

.top {
	width: 100vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: black;
	height: 40vh;
}

.topline1 {
	color: white;
	font-size: 7vw;
	margin-bottom: 40px;
}

.link1 {
	text-decoration: none;
}

.topline2 {
	background-color: white;
	color: rgb(145, 136, 136);
	padding: 10px;
	width: auto;
	display: flex;
	justify-content: center;
	font-size: 4vw;
	font-weight: 500;
}

.link2 {
	text-decoration: none;
}

.body {
	display: grid;
	grid-template-columns: auto auto auto;
	justify-content: center;
	align-items: center;
	margin-top: 30px;
	/* margin-left: 30px;
	margin-right: 20px; */
	max-width: 70vw;
}
.body__items {
	width: fit-content;
	display: grid;
	justify-content: center;
	background-color: white;
	color: rgb(145, 136, 136);
	margin: 15px;
	border-radius: 5px;
	padding: 15px;
	border-style: solid;
	border-width: 2px;
	/* border: thin 5px red; */
	/* border-width: th;
	border-color: blue; */
}

.bottom {
	/* display: flex; */
	/* justify-content: center; */
	background-color: rgb(70, 70, 202);
	padding: 20px;
	width: 70vw;
	border-radius: 20px;
	color: white;
	margin-top: 30px;
	margin-bottom: 20px;
}

@media only screen and (max-width: 600px) {
	.body {
		/* background-color: red; */
		display: flex;
		flex-direction: column;
		width: auto;
	}
}
@media only screen and (max-width: 600px) {
	.topline2 {
		/* background-color: red; */
		font-size: 4vw;
		font-weight: 600;
		width: auto;
	}
}
