.integratewallets {
	width: 100%;
	background-color: rgb(248, 246, 246);
	margin-top: -4vh;
}
.integratewallets__top {
	background-color: rgb(248, 246, 246);
}

.integratewallets__top1 {
	display: flex;
	justify-content: center;
	font-size: 50px;
	font-weight: 700;
}

.integratewallets__top2 > p {
	margin-top: 10px;
	background-color: rgb(70, 70, 202);
	color: white;
	display: flex;
	justify-content: center;
	padding: 30px;
	font-size: 6vw;
}
.integratewallets__top2 > h3 {
	display: flex;
	justify-content: center;
	padding-bottom: 30px;
	color: rgb(145, 136, 136);
}

.integratewallets__body {
	display: grid;
	grid-template-columns: auto auto;
	justify-content: center;
	align-items: center;
	/* margin-left: 20px; */
	/* margin-right: 20px; */
}

.wallet {
	width: 30vw;
	height: 35vh;
	padding: 10px;
	margin-left: 20px;
}

.wallet__link {
	text-decoration: none;
}
.wallet__img {
	box-shadow: 5px 5px #888888;
	width: 100px;
	background-color: white;
	height: 100px;
	padding: 10px;
	border-radius: 10px;
}

.wallet__name {
	font-weight: 700;
	width: 100px;
	justify-content: center;
	align-items: center;
	display: flex;
	color: rgb(145, 136, 136);
}

.wallet__info {
	display: flex;
}
